export function tranferLanguage(category, name) {
    let searchAt;
    switch (category) {
        case "topics":
            searchAt = topics;
            break;
        case "colors":
            searchAt = colors;
            break;
        default:
            searchAt = styles;
    }
    return searchAt.options.find(option => option.path === name).optionName
}