import React from 'react'
import TemplateSection from './TemplateSection'

function TemplatesShow() {
    return (
        <div className="py-[72px] flex flex-col space-y-32">
            <TemplateSection 
                title="Cc Templates mi"
                path="/latest/1"
                order="create_at"
                search={null}
            />
            <TemplateSection 
                title="Cc Templates ti nhiu nht"
                path="/popular/1"
                order="downloaded"
                search={null}
            />
            <TemplateSection 
                title="S "
                path="/filter/infographic/1"
                search="infographic"
                order={null}
            />
        </div>
    )
}

export default TemplatesShow
