// i tng 'Validator'
export default function Validator(options) {
    function getParent(element, selector) {
        while (element.parentElement) {
            if (element.parentElement.matches(selector)) {
                return element.parentElement;
            }
            element = element.parentElement;
        }
    }

     //Hm thc hin validate
     function validate(inputElement, rule, errorElement) {
        var errorMessage;
        
        // Ly ra cc rule ca selector
        var rules = selectorRules[rule.selector];

        // Lp qua tng rule v kim tra
        // Nu c li th dng vic kim tra
        for (var i = 0; i < rules.length; i++) {
            switch (inputElement.type) {
                case 'radio':
                case 'checkbox':
                    errorMessage = rules[i](
                        formElement.querySelector(rule.selector + ':checked')
                    )
                    break;
                default:
                    errorMessage = rules[i](inputElement.value);
            }
            if (errorMessage) break;
        }

         if (errorMessage) {
            errorElement.innerText = errorMessage;
            getParent(inputElement, options.formGroupSelector).classList.add('invalid')
        } else {
            removeErrorMessage(errorElement, inputElement);
        }

        return !errorMessage;
    }

    function removeErrorMessage(errorElement, inputElement) {
        errorElement.innerText = '';
        getParent(inputElement, options.formGroupSelector).classList.remove('invalid');
    }
    
    // Ly element ca form cn validate
    var formElement = document.querySelector(options.form);
    var selectorRules = {};
    
    if (formElement) {
        formElement.onsubmit = function(e) {
            e.preventDefault();
            
            var isFormValid = true;

             // Lp qua mi rule v x l
            options.rules.forEach(function(rule) {
                var inputElement = formElement.querySelector(rule.selector);
                var errorElement = getParent(inputElement, options.formGroupSelector).querySelector(options.errorSelector);
                var isValid = validate(inputElement, rule, errorElement);
                if (!isValid) {
                    isFormValid = false;
                }
            });
            
            if(isFormValid) {
                // Submit vi JS
                if(typeof options.onSubmit === 'function') {
                    // var enableInputs = formElement.querySelectorAll('[name]');
                    // var formValues = Array.from(enableInputs).reduce(function(values, input) {
                    //     switch(input.type) {
                    //         case 'radio':
                    //             if (input.matches(':checked'))
                    //                 values[input.name] = input.value;
                    //             break;
                    //         case 'checkbox':
                    //             if(input.matches(':checked')) {
                    //                 if (Array.isArray(values[input.name])) {
                    //                     values[input.name].push(input.value);
                    //                 }

                    //                 values[input.name] = [input.value];
                    //             } else {
                    //                 values[input.name] = "";
                    //                 return values;
                    //             }
                    //             break;
                    //         case 'file':
                    //             values[input.name] = input.files;
                    //             break;
                    //         default:
                    //             values[input.name] = input.value.trim();
                    //     }
                    //     return values;
                    // }, {});

                    options.onSubmit();
                } 
                // Submit vi hnh vi mc nh
                else {
                    formElement.submit();
                }
            }
        }

        // Lp qua mi rule v x l (lng nghe s kin)
        options.rules.forEach(function(rule) {

            // Lu li cc rules cho mi input 
            if (Array.isArray(selectorRules[rule.selector])) {
                selectorRules[rule.selector].push(rule.test);
            } else {
                selectorRules[rule.selector] = [rule.test];
            }
            

            var inputElements = formElement.querySelectorAll(rule.selector);

            Array.from(inputElements).forEach(function(inputElement) {
                var errorElement = getParent(inputElement, options.formGroupSelector).querySelector(options.errorSelector);
                if (inputElement) {
                    // X l trng hp blur khi inout
                    inputElement.onblur = function() {
                        validate(inputElement, rule, errorElement)
                    }
                    // X l mi  ngi dng nhp input
                    inputElement.oninput = function() {
                        removeErrorMessage(errorElement, inputElement);
                    }
                }
            });
        });
    }
}

// nh ngha cc rules (phng thc)
// Nguyn tc ca cc rules:
// 1. Khi c li => Tr ra message li
// 2. Khi hp l => Khng tr ra g c
Validator.isRequire = function(selector, message) {
    return {
        selector: selector,
        test: function(value) {
            return value ? undefined : message || 'Vui lng nhp trng ny';
        }
    }
}

Validator.isEmail = function(selector, message) {
    return {
        selector: selector,
        test: function(value) {
            var regex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
            return regex.test(value) ? undefined : message || 'Trng ny phi l Email'
        }
    }
}

Validator.isNumber = function(selector, message) {
    return {
        selector: selector,
        test: function(value) {
            var regex = /^\d+$/;
            return regex.test(value) ? undefined : message || 'Trng ny ch c nhp s'
        }
    }
}

Validator.minLength = function(selector, min, message) {
    return {
        selector: selector,
        test: function(value) {
            return value.length >= min ? undefined : message || `Vui lng nhp ti thiu ${min} k t` ;
        }
    }
}

Validator.maxLength = function(selector, max, message) {
    return {
        selector: selector,
        test: function(value) {
            return value.length <= max ? undefined : message || `Vui lng khng nhp qu ${max} k t` ;
        }
    }
}

Validator.isConfirmed = function(selector, getConfirmedValue, message) {
    return {
        selector: selector,
        test: function(value) {
            return value === getConfirmedValue() ? undefined : message || 'Gi tr nhp vo khng chnh xc';
        }
    }

}