import React from 'react'
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
import { Link } from 'react-router-dom';

function Footer() {
    return (
        <div className="text-white py-10 bg-[#181b44] mt-10 body-container grid grid-cols-1 gap-y-5 sm:grid-cols-2 xl:grid-cols-3 sm:gap-x-28 sm:gap-y-7">
            <div>
                <div className="font-medium text-2xl mb-3">Thit k chuyn nghip</div>
                <p className="text-lg">Cc mu Template ca Smart Points bao gm y  cc thnh phn h tr vic truyn ti ni dung ca bn ng thi gy n tng vi ngi nghe.</p>
            </div>
            <div>
                <div className="font-medium text-2xl mb-3">H tr mua bn cc Template</div>
                <p className="text-lg">Smart Points cung cp mi trng cho cc nh thit k template c th chia s cc mu template ca mnh hoc c th bn chng cho nhng ngi dng  nng cp ti khon.</p>
            </div>
            <div>
                <div className="font-medium text-2xl mb-3">Lin h</div>
                <div className="flex items-center space-x-4">
                    <Link to="/">
                        <FacebookIcon />
                    </Link>
                    <Link to="/">
                        <InstagramIcon />
                    </Link>
                    <Link to="/">
                        <TwitterIcon />
                    </Link>
                </div>
            </div>
        </div>
    )
}

export default Footer
